import { motion } from "framer-motion";
import { NavLink } from 'react-router-dom';
import { destroySession } from '../actions/userActions'
import { useSelector, useDispatch } from 'react-redux'

export default function Navbar() {
    const dispatch = useDispatch()
    const user = useSelector(state=> state.currentUser)

  return(
    <motion.nav className="navbar navbar-light container-fluid" 
        initial={{ y: '-100vh' }}
        animate={{ y: 0 }}
        transition={{ delay: 1.5 }}
    >   
        <ul className="navbar-nav">
            <li className="nav-item">
            <NavLink to='/' className="nav-link">
              <img src='https://kavaglam.s3.ca-central-1.amazonaws.com/kavaglam_logo_long.png' height='70px' alt="Kavaglam Logo" />
            </NavLink>
            </li>
        </ul>
        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#collapsibleNavbar">
            <span className="navbar-toggler-icon"></span>
            <div className="collapse navbar-collapse justify-content-between" id="collapsibleNavbar" >
            <ul className="navbar-nav p-4" >
                <li className="nav-item">
                    <NavLink to='/' className="nav-link">Home</NavLink>
                </li>
                <li className="nav-item">
                    <NavLink to='/products' className="nav-link">Products</NavLink>
                </li>
                <li className="nav-item">
                    <NavLink to='/cart' className="nav-link">Cart</NavLink>
                </li>
                <li className="nav-item">
                    <NavLink to='/sign_in' className="nav-link">Sign In</NavLink>
                </li>
                <li className="nav-item">
                    <NavLink to='/sign_up' className="nav-link">Sign Up</NavLink>
                </li>
                <li className="nav-item">
                    <input type='button' onClick={()=>dispatch(destroySession())} className="nav-link" value='signout'/>
                </li>
                {user.admin &&  (
                    <li className="nav-item">
                        <NavLink to='/admin' className="nav-link">Admin</NavLink>
                    </li>
                )}
            </ul>
        </div>
        </button>
    </motion.nav>
  )
}


